@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
html, body, #root, .App {
  height: 100%;
}

* {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Open Sans',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.timer{height:100%;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:space-around}.timer__time{display:flex;justify-content:center;align-items:center;font-size:40px;font-weight:bold;height:300px;width:300px;color:#1e1e1e;border-radius:100%;background:linear-gradient(145deg, #fff, #e6e6e6);box-shadow:10px 10px 20px #a8a8a8, -10px -10px 20px #ffffff;position:relative;-webkit-transition:.3s ease-in-out color;transition:.3s ease-in-out color}.timer__time::after{content:'';position:absolute;height:80%;width:80%;background-color:transparent;border-radius:50%;box-shadow:inset -3px -3px 7px #e6e6e6,inset 3px 3px 5px rgba(94,104,121,0.671)}.timer__time_paused{color:#646464}.timer__progress-bar{height:300px;width:300px}.timer__progress-bar circle{fill:transparent}.timer__progress-bar path{stroke:#36f;-webkit-transition:1s ease-in-out all;transition:1s ease-in-out all}.timer__controller{display:flex;align-items:center;justify-content:center}.timer__button{cursor:pointer;margin:30px;height:75px;width:75px;font-weight:bold;border-radius:205px;background:linear-gradient(145deg, #fff, #e6e6e6);box-shadow:8px 8px 15px #a8a8a8, -8px -8px 15px #ffffff;outline:none;border:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;-webkit-tap-highlight-color:transparent;-webkit-transition:.3s ease-in-out box-shadow;transition:.3s ease-in-out box-shadow}.timer__button:hover{box-shadow:4px 4px 8px #a8a8a8, -4px -4px 8px #ffffff}.timer__button:focus{box-shadow:2px 2px 4px #a8a8a8, -2px -2px 4px #ffffff}.timer__button:disabled{cursor:auto;cursor:initial;box-shadow:none}.timer__button:disabled .icon svg{fill:#646464}

.icon{display:flex;justify-content:center}.icon svg{width:30px}

